	module.exports = {"data":{"talkJson":{"description":"Barbara Liskov, Professor of Electrical Engineering and Computer Science\nat the Massachusetts Institute of Technology, discusses \"Programming the Turing Machine\" in a lecture given on the occasion of Princeton University's centennial celebration of Alan Turing. Learn more at www.princeton.edu/turing\n\n#turingprinceton","meta":{"duration":"PT1H6M10S"},"presenter":{"name":null},"slug":"barbara-liskov-programming-the-turing-machine","source":{"id":"ibRar7sWulM","provider":"youtube"},"tag":["Barbara Liskov","MIT","Turing machine","Princeton","Turing centennial","Alan Turing","#turingprinceton"],"title":"Barbara Liskov: Programming the Turing Machine"},"allTalkJson":{"edges":[{"node":{"description":"Philip Wadler, Professor of Theoretical Computer Science at the University of Edinburgh, contends that the foundations of computing lay in a coincidence: Church's lambda calculus (1933), Herbrand and Godel's recursive functions (1934), and Turing's machines (1935) all defined the same model of computation. \n\n\nHe gave this lecture on the occasion of Princeton University's centennial celebration of Alan Turing. Learn more at www.princeton.edu/turing\n\n#turingprinceton","meta":{"duration":"PT1H3M41S"},"presenter":{"name":null},"source":{"id":"2PJ_DbKGFUA","provider":"youtube"},"slug":"philip-wadler-churchs-coincidences","tag":["Philip Wadler","University of Edinburgh","Princeton","Alan Turing","computer science","lambda calculus","Turing machine","#turingprinceton"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/2PJ_DbKGFUA/default.jpg","height":90,"width":120}},"title":"Philip Wadler: Church's Coincidences"}},{"node":{"description":"Talk by ACM A.M. Turing Laureate Dana S. Scott during the ACM A.M. Turing Centenary Celebration, June, 2012.\n\nAbstract:\n\nA very fast development in the early 1930s, following Hilbert's codification of Mathematical Logic, led to the Incompleteness Theorems, Computable Functions, Undecidability Theorems, and the general formulation of recursive Function Theory. The so-called Lambda Calculus played a key role. The history of these developments will be traced, and the much later place of Lambda Calculus in Mathematics and Programming-Language Theory will be outlined.","meta":{"duration":"PT30M8S"},"presenter":{"name":null},"source":{"id":"SphBW9ILVPU","provider":"youtube"},"slug":"lambda-calculus-then-and-now","tag":["Calculus","Dana S. Scott","ACM","ACM A.M. Turing Award","Alan Turing","Nobel Prize","in","computing","Contest","Entry","Computer","Electronics","Gadget","Technology","Space","Physics","Mechanics","Experiment","Math"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/SphBW9ILVPU/default.jpg","height":90,"width":120}},"title":"Lambda Calculus Then and Now"}},{"node":{"description":"Richard Stallman in Zurich: The father of the movement for free software about his rebellion against administrative slack at MIT, about \"foolish\" Apple aficionados, current fights against evil software companies and Swiss national hero William Tell, who could serve as a role model in the fight against an unjust copyright.\n\nBy the way: After the camera was turned off, Richard and I talked about some other topics: Ads in the web and his Free Software song. This part is available as audio recording here, just skip to minute 20:37: http://www.stadtfilter.ch/DigitalPodcast/Digital20160517","meta":{"duration":"PT55M31S"},"presenter":{"name":"Richard Stallman"},"source":{"id":"fkkDvKGcNSo","provider":"youtube"},"slug":"apple-fanboys-are-foolish-people","tag":["Free Software foundation","Interview","MIT","Open Source"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/fkkDvKGcNSo/default.jpg","height":90,"width":120}},"title":"Apple fanboys are foolish people"}},{"node":{"description":"Andrew Odlyzko, Professor of Mathematics at the University of Minnesota, discusses \"Turing and the Riemann zeta function\" in a lecture given on the occasion of Princeton University's centennial celebration of Alan Turing. Learn more at www.princeton.edu/turing\n\n#turingprinceton","meta":{"duration":"PT1H6M54S"},"presenter":{"name":null},"source":{"id":"pQS7X_8iTCA","provider":"youtube"},"slug":"andrew-odlyzko-turing-and-the-riemann-zeta-function","tag":["in","lecture","given","on","the","occasion","of","Princeton","University's","centennial","celebration","Alan","Turing.","Learn","more","at","Odlyzko","#turingprinceton","University of Minnesota","Riemann zeta function","Alan Turing","Turing centennial","computer science"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/pQS7X_8iTCA/default.jpg","height":90,"width":120}},"title":"Andrew Odlyzko: Turing and the Riemann zeta function"}}]}},"pathContext":{"slug":"barbara-liskov-programming-the-turing-machine","tags":["Barbara Liskov","MIT","Turing machine","Princeton","Turing centennial","Alan Turing","#turingprinceton"]}}

